import React, { useState } from "react";
import Grid from "@material-ui/core/Grid";
import TextField from "@material-ui/core/TextField";
import { ThemeProvider } from "@material-ui/styles";
import useStyles from "./styles.js";
import { theme } from "./styles";
import FileBase from "react-file-base64";
import { postActions } from "../../../store/postSlice";
import { useSelector, useDispatch } from "react-redux";

const Form = () => {
  const classes = useStyles();
  const dispatch = useDispatch();
  const [postData, setPostData] = useState({
    content: "",
    title: "",
    description: "",
    tag: "",
    selectedFile: "",
  });
  const postArray = useSelector((state) => state.post.postArray);

  const clear = () => {
    setPostData({
      content: "",
      title: "",
      description: "",
      tag: "",
      selectedFile: "",
    });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    dispatch(postActions.addPost(postData));
    clear();
  };
  return (
    <div className={classes.root}>
      {console.log(postArray)}
      <form
        autoComplete="off"
        noValidate
        className={`${classes.root} ${classes.form}`}
        onSubmit={handleSubmit}
      >
        <Grid container spacing={0}>
          <Grid item xs={12} sm={6} className={classes.contentContainer}>
            <h3>Content:</h3>
            <ThemeProvider theme={theme}>
              <TextField
                id="outlined-multiline-static"
                multiline
                rows={4}
                variant="outlined"
                className={classes.textField}
                value={postData.content}
                onChange={(e) =>
                  setPostData({ ...postData, content: e.target.value })
                }
              />
            </ThemeProvider>
          </Grid>
          <Grid item xs={12} sm={6} className={classes.rightContainer}>
            <div className={classes.titleContainer}>
              <h3>Title:</h3>
              <TextField
                id="outlined-multiline-static"
                multiline
                rows={4}
                variant="outlined"
                className={classes.textFieldTitle}
                value={postData.title}
                onChange={(e) =>
                  setPostData({ ...postData, title: e.target.value })
                }
                inputProps={{
                  style: {
                    height: "3vh",
                    padding: "0",
                    margin: "0",
                  },
                }}
              />
            </div>
            <div className={classes.titleContainer}>
              <h3>Description:</h3>
              <TextField
                id="outlined-multiline-static"
                multiline
                rows={4}
                variant="outlined"
                className={classes.textFieldDescription}
                value={postData.description}
                onChange={(e) =>
                  setPostData({ ...postData, description: e.target.value })
                }
                inputProps={{
                  style: {
                    height: "100%",
                    padding: "0",
                    margin: "0",
                  },
                }}
              />
            </div>
            <div className={classes.tagContainer}>
              <h3>Tag:</h3>
              <TextField
                id="outlined-multiline-static"
                multiline
                rows={4}
                variant="outlined"
                className={classes.textFieldTag}
                value={postData.tag}
                onChange={(e) => {
                  setPostData({ ...postData, tag: e.target.value });
                }}
                inputProps={{
                  style: {
                    height: "100%",
                    padding: "0",
                    margin: "0",
                  },
                }}
              />
            </div>
            <div className={classes.fileInput}>
              <h5>Add Image:</h5>
              <FileBase
                type="file"
                multiple={false}
                onDone={({ base64 }) =>
                  setPostData({ ...postData, selectedFile: base64 })
                }
              />
            </div>
            <button
              style={{
                marginTop: "1.5em",
                width: "15%",
                alignSelf: "flex-end",
                marginRight: "5em",
                border: "0px",
                height: "3em",
                borderRadius: "25px",
                background: "#454444",
                color: "#DEBA3B",
              }}
            >
              Submit
            </button>
          </Grid>
        </Grid>
      </form>
    </div>
  );
};

export default Form;
