import React from "react";
import { useParams } from "react-router-dom";
import { postArrayDummy } from "../../data/PostArray";
import styles from "./BlogDetails.module.css";
import NavBar from "../NavBar/NavBar";

const BlogDetails = (props) => {
  const { id } = useParams();
  const singlePost = postArrayDummy.filter((post) => post._id === id);
  console.log(singlePost);
  console.log(props);
  return (
    <>
      <NavBar />
      <div className={styles.blogContainer}>
        <h1 className={styles.title}>{singlePost[0].title}</h1>
        <img
          className={styles.blogImage}
          src="https://fiverr-res.cloudinary.com/images/q_auto,f_auto/gigs/111483751/original/df06db65e81a7c55478cab18e9ed8068594afca1/do-story-writting-creative-writting-and-many-more.jpeg"
          alt=""
        />
        <p className={styles.content}>{singlePost[0].content}</p>
        <div className={styles.comment}>
          <h3 style={{ fontSize: "1.3em", color: "#454444" }}>Comment</h3>
          <textarea></textarea>
          <button>Add comment</button>
        </div>
      </div>
    </>
  );
};

export default BlogDetails;
